  .header {
    padding: 27px 0;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 16px 0;
  }
}
.header.is-hidden {
  transform: translateY(-100%);
}
.header__overlay {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  display: none;
  background: rgba(0, 43, 102, 0.5);
  cursor: pointer;
}
.is-open .header__overlay {
  display: block;
}
.header .header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  display: flex;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .header__logo svg {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .header__logo svg {
    width: 42px;
    height: 22px;
  }
}
.is-open .header__logo {
  transform: translateX(11.6vw);
  z-index: 25;
  color: #011c40;
}
@media (hover: hover) {
  .header__logo:hover:not(.focus-visible):not(span) {
    opacity: 0.6;
  }
}
@media (max-width: 767px) {
  .header__nav {
    background-color: #f9fbfd;
    bottom: 0;
    display: flex;
    flex-direction: column;
    left: 0;
    opacity: 0;
    margin: 0;
    padding: 84px 25px 20px;
    position: fixed;
    pointer-events: none;
    top: 0;
    right: 0;
    visibility: hidden;
    z-index: 20;
  }
}
.is-open .header__nav {
  width: 88.9vw;
  right: 0;
  left: auto;
  opacity: 1;
  pointer-events: all;
  visibility: visible;
}
.is-open .header__nav .header__item {
  opacity: 1;
  transform: translateX(0);
}
.header__list {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (max-width: 767px) {
  .header__list {
    flex-direction: column;
    max-width: unset;
    align-items: flex-start;
    margin: 0 0 62px 0;
  }
}
@media (max-width: 767px) {
  .header__item {
    width: 100%;
    opacity: 0;
    transition: all ease-out 0.15s;
    transform: translateX(-15px);
  }
}
.header__item:not(:last-child) {
  margin: 0 34px 0 0;
}
@media (max-width: 1023px) {
  .header__item:not(:last-child) {
    margin: 0 24px 0 0;
  }
}
@media (max-width: 767px) {
  .header__item:not(:last-child) {
    margin: 0 0 28px 0;
  }
}
.header__link {
  font-family: "Montserrat", "Tahoma", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd;
  transition: color 0.3s ease;
}
@media (max-width: 1023px) {
  .header__link {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
