    display: flex;
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }
}
@media (hover: hover) {
  .header__link:hover:not(.focus-visible):not(span) {
    opacity: 0.6;
  }
}
.header__burger {
  align-items: center;
  background-color: transparent;
  border: none;
  display: none;
  padding: 0;
  position: relative;
  z-index: 25;
}
@media (max-width: 767px) {
  .header__burger {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 30px;
    height: 30px;
    transition: all 0.3s ease;
    cursor: pointer;
  }
}
@media (hover: hover) {
  .header__burger:hover:not(.focus-visible):not(span) {
    opacity: 0.6;
  }
}
.header__burger .is-open .header__burger-icon {
  opacity: 1;
  pointer-events: all;
  transition: opacity 0.3s ease 0.2s;
}
.header__burger-icon {
  stroke: #f9fbfd;
  transition: opacity 0.3s ease;
  pointer-events: none;
  position: absolute;
  left: 6px;
  cursor: pointer;
}
.is-open .header__burger-icon {
  opacity: 1;
  pointer-events: all;
  transition: opacity 0.3s ease 0.2s;
}
.is-open .header__burger-open {
  display: none;
}
.header__burger-close {
  display: none;
}
.is-open .header__burger-close {
  display: block;
}

